import { Component, OnInit } from '@angular/core';
import { KnowledgeService } from '../../../services/knowledge.service';

@Component({
  selector: 'app-image-sections',
  templateUrl: './image-sections.component.html',
  styleUrls: ['./image-sections.component.css']
})
export class ImageSectionsComponent implements OnInit {

  dhruvitDetails=[];

  constructor(private _knowledgeservice : KnowledgeService) { }

  ngOnInit(){
    this._knowledgeservice.getDhruvitdetail()
              .subscribe(data => this.dhruvitDetails = data);
  }

}
