import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Knowledge } from '../knowledge';
import { DhruvitDetails } from '../interfaces/dhruvit-details';
import { Education } from '../interfaces/education';
import { PersonalProject } from '../interfaces/personal-project';
import { Internship } from '../interfaces/internship';
import { from, Observable } from 'rxjs';
// import 'rxjs/add/operator/catch';
// import 'rxjs/add/observable/throw';

@Injectable({
  providedIn: 'root'
})
export class KnowledgeService {

  private _url:string="../../assets/data/knowledge.json";
  private _url1:string="../../assets/data/dhruvitDetail.json";
  private education_link:string="../../assets/data/education.json";
  private projects_link:string="../../assets/data/pproject.json";
  private internship_link="../../assets/data/internship.json";

  constructor(private http:HttpClient) { }

  getKnowledge(): Observable<Knowledge[]> {
    return this.http.get<Knowledge[]>(this._url);
  }

  getDhruvitdetail(): Observable<DhruvitDetails[]>{
    return this.http.get<DhruvitDetails[]>(this._url1);
  }

  getEducation(): Observable<Education[]>{
    return this.http.get<Education[]>(this.education_link);
  }

  getProjects(): Observable<PersonalProject[]> {
    return this.http.get<PersonalProject[]>(this.projects_link);
  }


  getInternship(): Observable<Internship[]>{
    return this.http.get<Internship[]>(this.internship_link);
  }




  // errorHandler(error: HttpErrorResponse){
  //   return Observable.throw(error.message || "server error");
  // }
}




