import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-resume-header',
  templateUrl: './resume-header.component.html',
  styleUrls: ['./resume-header.component.css']
})
export class ResumeHeaderComponent implements OnInit {

  constructor() { }

  burger = document.querySelector('.burger')
  navbar = document.querySelector('.navbar')
  leftNav = document.querySelector('.leftNav')
  navList = document.querySelector('.nav-list')

  hamburger(){
    this.leftNav.classList.toggle('v-class');
    this.navList.classList.toggle('v-class');
    this.navbar.classList.toggle('h-nav');
  }

  toAbout(){
    document.getElementById("about").scrollIntoView({behavior:"smooth"});
  }

  toEducation(){
    document.getElementById("education").scrollIntoView({behavior:"smooth"});
  }

  toInternship(){
    document.getElementById("internship").scrollIntoView({behavior:"smooth"});
  }

  toProject(){
    document.getElementById("projects").scrollIntoView({behavior:"smooth"});
  }

  toKnowledge(){
    document.getElementById("knowledge").scrollIntoView({behavior:"smooth"});
  }


  ngOnInit(): void {
  }

}
