import { Component, OnInit } from '@angular/core';
import { KnowledgeService } from 'src/app/services/knowledge.service';

@Component({
  selector: 'app-internship',
  templateUrl: './internship.component.html',
  styleUrls: ['./internship.component.css']
})
export class InternshipComponent implements OnInit {

  public internshipDatas=[];

  constructor(private _knowledgeService : KnowledgeService) { }

  ngOnInit(): void {
    this._knowledgeService.getInternship()
                    .subscribe(data => this.internshipDatas = data);
  }

}
