import { Component, OnInit } from '@angular/core';
import { KnowledgeService } from 'src/app/services/knowledge.service';

@Component({
  selector: 'app-resume-middle',
  templateUrl: './resume-middle.component.html',
  styleUrls: ['./resume-middle.component.css']
})
export class ResumeMiddleComponent implements OnInit {

  public knowledges=[];

  constructor(private _knowledgeservice : KnowledgeService) { }


  toAbout(){
    document.getElementById("about").scrollIntoView({behavior:"smooth"});
  }


  ngOnInit() {
    this._knowledgeservice.getKnowledge()
              .subscribe(data => this.knowledges = data);
  }

}
